    <l-layer-group layer-type="overlay" name="<b>Satellite / Street Basemap</b>">
        <l-tile-layer :url="osm"></l-tile-layer>
    </l-layer-group>
    <l-layer-group layer-type="basemap" name="<b>Satellite Basemap</b>">
        <l-tile-layer :url="satellite"></l-tile-layer>
    </l-layer-group>

    <l-layer-group layer-type="overlay" name="<b>Boundary</b>">
     <l-geo-json
          v-for="Boundary in map.Boundary.features"
          :key="Boundary.Id"
          :geojson="Boundary" 
          :options-style="Boundarystyle('utility', Boundary)"
          :weight="1"
      
      ></l-geo-json>
    </l-layer-group>
    
    <l-layer-group layer-type="overlay" name="<b>GDA Master Plan</b>">
    <l-geo-json
      v-for="Region in map.Region.features"
      :key="Region.Legend"
      :geojson="Region" 
      :options="RegionOptions(Region)"
      :options-style="Regionstyle('utility', Region)"
      :weight="1"
      :visible="getLayerVisibility"
    ></l-geo-json>
    </l-layer-group>

    <l-layer-group layer-type="overlay" name="<b>Societies Maps</b>">
          <l-geo-json
            v-for="Globiz in map.Globiz.features"
            :key="Globiz.Label"
            :geojson="Globiz" 
            :options="GlobizOptions(Globiz)"
            :options-style="Globizstyle('Globiz', Globiz)"
            @click="fetchPlotInfo(Globiz)"
            :weight="1"
            
          ></l-geo-json>
    </l-layer-group>
    
    <l-layer-group v-if="show_moza===true" layer-type="overlay" name="<b>Khasra Maps</b>">
          <l-geo-json
            v-for="Khasra in map.Khasras.features"
            :key="Khasra.khasra_no"
            :geojson="Khasra" 
            :options="KhasraOptions(Khasra)"
            :options-style="Khasrastyle('Khasra', Khasra)"
           
            :weight="1"
            
          ></l-geo-json>
    </l-layer-group>


