    ></l-geo-json> -->

    
    <!-- Icon Issued -->
    <l-layer-group layer-type="overlay" name="<b>NOC Issued Societies</b>">
    <l-marker
        v-for="Issue in map.Issued.features"
            :key="Issue.properties.SrNo"
            :lat-lng="[Issue.properties.Lat_New,Issue.properties.Long_New]"
            @click="fetchTowerInfo(Issue);  toggleLeftDrawer()"
    >
              <l-tooltip>
                <h3> Project: {{ Issue.properties.Project_Na }} <hr> Status: {{ Issue.properties.NOCStatus }} </h3>
              </l-tooltip>
    </l-marker>
    </l-layer-group>

    <l-layer-group layer-type="overlay" name="<b>NOC Expired Societies</b>">
