     <l-marker
        v-for="Expire in map.Expired.features"
            :key="Expire.properties.SrNo"
            :lat-lng="[Expire.properties.Lat_New,Expire.properties.Long_New]"
            :icon="icon_Expired"
            @click="fetchTowerInfo(Expire); toggleLeftDrawer()"
    >
               <l-tooltip>
                <h3> Project: {{ Expire.properties.Project_Na }} <hr> Status: {{ Expire.properties.NOCStatus }} </h3>
              </l-tooltip>
    </l-marker>
    </l-layer-group>

