  computed: {
    ...mapGetters([
      "getPlots",
      "getDealInfo",
      "getZoom",
      "getCenter",
      "getLabels",
      "getSelectedPlot",
      "getSelectedDeal",
      "getInfoDialog",
      "getLayerVisibility"
    ])

  },
  methods: {

    reports_route() {
      this.$router.push({path: `/Reports/`});
    },
    society_redirecting (){
      return alert("Page will be Re-directed to ESTATECH ERP")

    },           
    sorrywarning(){
      return alert('Sorry! This function is not currently available.')
      
    },
    editalert(){
      return alert('Please Login First')
      // console.log('data_In_Nav:',this.getSoc_Summary_ByMap),
     
    },
    toggletabs(){
        this.$store.commit("setTabsVisibility", !this.getTabsVisibility);
    },
    toggleLeftDrawer() {
      this.$store.commit("setLeftDrawer", !this.getLeftDrawer);
    },

    fetchSocietyInfo_byselect() {

     var society = this.map.all_societies.features.filter(item => { 
     if(this.societyName === item.properties.Project_Na) { 
          return item;
      }
        });
    this.$store.commit("setSoc_Summary_ByMap", society[0].properties);
    
    var geojson = society[0].geometry;
    this.$refs.map.mapObject.flyTo(
        [geojson.coordinates[1], geojson.coordinates[0]],
        18
      );
     
      this.$store.commit("setLeftDrawer", !this.getLeftDrawer);
      
      
      
     },
    
    mozaZoom(){
      this.$refs.map.mapObject.flyTo(
        [25.2454,62.2569],
        14
      );
      this.khasraBtn=true;
    },
    khasraZoom(){
     var plot = this.map.Khasras.features.filter(item => { 
     if(this.khasraName === item.properties.khasra_no) { 
          return item;
      }
        });
      // console.log("centroid:",plot);
    // this.plot_rec = plot[0].properties;
    // console.log('plot_rec',this.plot_rec);
    var geojson = plot[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("geojson lon:",geojson.coordinates[0])
    // console.log("centroid:",centroid)

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      }); 
      
    },


     fetchPlotInfo_byselect() {

     var plot = this.map.Globiz.features.filter(item => { 
     if(this.plotID === item.properties.plot_id || this.OwnerID === item.properties.Area_Kanal) { 
          return item;
      }
        });
        // console.log('plot_rec11',this.plotID);
    this.plot_rec = plot[0].properties;
    // console.log('plot_rec',this.plot_rec);
    var geojson = plot[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("geojson lon:",geojson.coordinates[0])
    // console.log("geojson lat:",geojson.coordinates[1])

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });
      this.$store.commit("setPlotInfoSkeleton", true);
      this.$store.commit("setInfoDialog", !this.getInfoDialog);
     },

     fetchOwnerInfo_byselect() {

     var plot = this.map.Globiz.features.filter(item => { 
     if(this.OwnerID === item.properties.Area_Kanal) { 
          return item;
      }
        });
        // console.log('plot_rec11',this.plotID);
    this.plot_rec = plot[0].properties;
    // console.log('plot_rec',this.plot_rec);
    var geojson = plot[0].geometry;
    var ring = geojson.coordinates[0][0];
    var poly = turf.polygon([ring]);
    var centroid = turf.centroid(poly);
    // console.log("geojson lon:",geojson.coordinates[0])
    // console.log("geojson lat:",geojson.coordinates[1])

    this.$refs.map.mapObject.flyTo(
        [centroid.geometry.coordinates[1], centroid.geometry.coordinates[0]],
        18
      );
          this.$store.dispatch("markerpoint", {
        lat: centroid.geometry.coordinates[1],
        lon: centroid.geometry.coordinates[0],
      });
      this.$store.commit("setPlotInfoSkeleton", true);
      this.$store.commit("setInfoDialog", !this.getInfoDialog);
     },

    fetchPlotInfo(plot_approved) {
      // console.log('clicked Plot:', plot_approved)
    //   var plot = this.map.Globiz.features.filter(item => { 
    //  if(this.plot_approved === item.properties.plot_id) { 
    //       return item;
    //   }
    //     });
      this.plot_rec = plot_approved.properties;
      var polygon = plot_approved.geometry;
      var center = turf.centroid(polygon);
      this.$refs.map.mapObject.flyTo(
        [center.geometry.coordinates[1], center.geometry.coordinates[0]],
        18
      );
      this.$store.dispatch("fetchPlotInfo", {
        // url: this.api.url,
        // id: plot.new_id
        info: plot_approved,
      });
      // this.$store.commit("setPlotInfoSkeleton", true);
      this.$store.commit("setInfoDialog", !this.getInfoDialog);

      this.$store.dispatch("markerpoint", {
        lat: center.geometry.coordinates[1],
        lon: center.geometry.coordinates[0],
      });
    },

    toggleInfoDialog() {
      this.$store.commit("setInfoDialog", !this.getInfoDialog);
    },
    toggleLayers(){
      this.$store.commit("setLayerVisibility", !this.getLayerVisibility);
      },
    zoomUpdate() {
      this.$store.commit("setZoom", this.$refs.map.mapObject.getZoom());
      // console.log("Zoom updated: ", this.$refs.map.mapObject.getZoom());
    },
    onEachFeatureOptions() {
      return (feature, layer) => {
        layer.bindTooltip(
          "<div style='text-transform:capitalize'>" + feature + "</div>",
          { permanent: false, sticky: true }
        );
      };
    },


    featureOptions(plot) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (feature) {
            layer.bindTooltip(`
                  <div>
                    <h2>Khasra Number: ${plot.plot_id}</h2>
                    
                  </div>
            `);
          }
        }
      };
    },

    UtilityOptions(utilities) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (utilities) {
            layer.bindTooltip(`
                  <div>
                    <h2>${utilities.properties.Type}</h2>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    RegionOptions(Region) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (Region) {
            layer.bindTooltip(`
                  <div>
                    <h2>${Region.properties.Legend}</h2>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    GlobizOptions(Globiz) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (Globiz) {
            layer.bindTooltip(`
                  <div>
                    <h4>${Globiz.properties.Label}</h4>
                    <hr>
                    <h4>Dimentions: ${Globiz.properties.Dimensions}</h4>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },
    KhasraOptions(Khasra) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (Khasra) {
            layer.bindTooltip(`
                  <div>
                    <h4>Khasra No: ${Khasra.properties.khasra_no}</h4>
                    <hr>
                    <h4>Status: ${Khasra.properties.Status}</h4>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

    BoundaryOptions(Boundary) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (Boundary) {
            layer.bindTooltip(`
                  <div>
                    <h2>${Boundary.properties.khasra_no}</h2>
                  </div>
                  
            `,
            {sticky: true }
            );
          }
        }
      };
    },

 
    labelOptions(plot) {
      return {
        onEachFeature: function onEachFeature(feature, layer) {
          if (feature) {
            layer
              .bindTooltip("<div>" + plot.plot_id + "</div>", {
                permanent: true,
                direction: "center",
                className: "my-labels-plots"
              })
              .openTooltip();
          }
        }
      };
    },


    style(feature, params) {
      if (feature == "plot") {
        return {
          strokeColor: "#333333",
          strokeWeight: 2,
          weight: 1,
          color: "#333333",
          opacity: 0.5,
          fillColor:
            params.moza_name != null
              ? this.pdfColors[params.moza_name]
              : "#ffb482",
          fillOpacity: this.Op_value/100
        };
      }
    },

    utilitystyle(feature, utilities) {
      if (feature== 'utility') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "#333333",
          opacity: 1,
          fillColor:
            utilities.properties.Type != null
              ? this.pdfColors[utilities.properties.Type]
              : "#ffb482",
          fillOpacity: 1,
         
        };
      }
    },

    Boundarystyle(feature,) {
      if (feature== 'utility') {
        return {
          strokeColor: "#ff4545",
          strokeWeight: 2,
          weight: 3,
          color: "#000000",
          opacity: 1,
          fillColor: "#000000",
            // Boundary.properties.Id != null
            //   ? this.pdfColors[Boundary.properties.khasra_no]
              // : "#242321",
          fillOpacity: 0,
         
        };
      }
    },



    Towerstyle(feature, Tower) {
      if (feature== 'utility') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "#333333",
          opacity: 1,
          fillColor:
            Tower.properties.Detail != null
              ? this.pdfColors[Tower.properties.officeID]
              : "#ffb482",
          fillOpacity: 1,
         
        };
      }
    },

    Regionstyle(feature,Region ) {
      if (feature== 'utility') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "red",
          opacity: 1,
          fillColor:
            Region.properties.Legend != null
              ? this.pdfColors[Region.properties.Legend]
              : "#ffb482",
          fillOpacity: 0.6,
         
        };
      }
    },

    Globizstyle(feature,Globiz ) {
      if (feature== 'Globiz') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "black",
          opacity: 1,
          fillColor:
            Globiz.properties.Color != null
              ? this.pdfColors[Globiz.properties.Color]
              : "#ffb482",
          fillOpacity: this.Op_value/100,
         
        };
      }
    },
     Khasrastyle(feature,Khasra ) {
      if (feature== 'Khasra') {
        return {
          strokeColor: "#333333",
          strokeWeight: 0.5,
          weight: 0.5,
          color: "black",
          opacity: 1,
          fillColor:
            Khasra.properties.Status != null
              ? this.pdfColors[Khasra.properties.Status]
              : "#ffb482",
          fillOpacity: this.Op_value/100,
         
        };
      }
    },
    
    fetchTowerInfo(Tower) {
      // var polygon = plot.st_asgeojson;
      // var center = turf.centroid(polygon);
      //  this.isHidden=false
      // console.log('state:',Tower)
      this.$refs.map.mapObject.flyTo(
        [Tower.geometry.coordinates[1],Tower.geometry.coordinates[0]],
        16.2
      );
      
      // if (Tower.properties.Project_Na === 'Globiz Phase-IV') {
      //   this.isHidden=false
      // }
      this.$store.dispatch("fetchTowerInfo", {
        url: Tower,
        id: Tower.properties.siteID
      });
      this.$store.commit("setSoc_Summary_ByMap", Tower.properties);

      // console.log('id_Map:', id)
      // this.$store.commit("setPlotInfoSkeleton", true);
      // this.$store.commit("setInfoDialog", !this.getInfoDialog);
    }
  },
  


  
  mounted() {
    this.$store.commit("setMap", this.$refs.map.mapObject);
    // this.loginForm=true;
    this.show_login=true;
  }
};
</script>

